  justify-content: center;
  align-items: center;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.backdrop.show {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.backdrop.show .modal, .backdrop:not(.is-hidden-header) .modal {
  opacity: 1;
  transform: translate(-50%, -50%)scale(1);
}

.backdrop.is-hidden-header {
